import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Resposta_auditoria } from '../../../modelo/Resposta_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'resposta_auditoria-dados-edit',   templateUrl: 'resposta_auditoria_dadosEdit.component.html' }) export class Resposta_auditoriaDadosEditComponent implements OnInit {    resposta_auditoria: Resposta_auditoria;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.resposta_auditoria.Resposta_auditoria";                 let id = +params['id'];         this.resposta_auditoria = new Resposta_auditoria();         this.resposta_auditoria.cd_resposta_auditoria = id;         o.objetoJson = this.resposta_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a resposta_auditoria. ';       }      this.resposta_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


